.left__avatar {
    width: auto;
}

.left__avatar .img {
    width: 100%;
    border-radius: 50%;
    object-fit: cover;
}

.record {
    display: block;
    border: none;
    outline: none;
    background: #FFC59E;
    box-shadow: 0px 4px 20px #FFC59E;
    border-radius: 33.5px;
    color: #fff;
    text-transform: uppercase;
    font-family: $font-futura;
    font-size: 20px;
    line-height: 20px;
    font-weight: 700;
    padding: 24px;
    margin-top: 30px;
    margin-bottom: 40px;
}

.responcive-button {
    background-color: transparent;
    display: block;
    border: 2px solid transparent;
    outline: none;
    color: #FFC59E;
    font-family: $font-futura;
    font-size: 20px;
    line-height: 20px;
    font-weight: 700;
    transition: 1s;
    padding: 5px;

    &:hover {
        border-bottom: 2px solid #FFC59E;
        transition: 1s;
    }
}

.black-doctor {
    font-family: $font-futura;
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    display: block;
    color: #59443F;
    margin: 20px 0;
    max-width: 689px;
}
.course-slider{
    margin-bottom: 50px;
}
.course-slide__card {
    background: #FFFFFF;
    border: 1px solid #EDE9E8;
    border-radius: 20px;
    padding-left: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: left;
}

.course-next{
    position: absolute;
    top: 30%;
    right: -5%;
    width: 40px;
    height: 40px;
    border: 1px solid transparent;
    border-radius: 50%;
    background: #FFC59E;
    box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.2);
}
.course-prev{
    display: none !important; 
}
.name-user{
    display: block;
    font-family: $font-futura;
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    display: block;
    color: #59443F;
    text-align: left;
}
.responsive__card{
    display: flex;
    background: #FFFFFF;
border: 1px solid #EDE9E8;
border-radius: 20px;
padding-left: 20px;
padding-top: 20px;
padding-right: 20px;
margin-bottom: 30px;
    max-width: 909px;
@include media ('>=md'){
    max-width: 600px;
}
@include media ('>=xl'){
    max-width: 909px;
}
}
.left-card{
    width: auto;
    margin-right: 10px;
}
.left-card img{
    width: 100%;
    height: auto;
    border-radius: 50%;
}