.history {
    background: #FBF8F8;
    padding-top: 50px;
    padding-bottom: 50px;
    @include media ('>=lg'){
        padding-bottom: 206px;
        padding-top: 180px;
    }

    &__title {
        font-family: $font-school;
        font-size: 40px;
        line-height: 40px;
        text-align: center;
        font-weight: 400;
        color: #080606;
    }

    &__text {
        font-family: $font-futura;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        color: #59443F;
    max-width: 479px;
    margin: 0 auto;
    }

    &__link {
        font-family: $font-futura;
        font-weight: 700;
        font-size: 20px;
        line-height: 20px;
        color: #FFC59E;
        text-transform: uppercase;
        position: relative;
        border-bottom: 2px solid transparent;
        transition: 0.5s;
       
        
    }

    &__link::after {
        position: absolute;
        content: url('../img/Path.svg');
        transition: 0.5s;
    }

    &__link:hover {
        color: #FFC59E;
        border-bottom: 2px solid #FFC59E;
        transition: 0.5s;
    }

    &__link:hover::after {
        margin-left: 7px;
        transition: 0.5s;
    }
    .link{
        max-width: 200px;
        margin: 0 auto;
        margin-top: 36px;
    }
}
