.why {
    background: #FFFEFE;
    padding-top: 50px;
    padding-bottom: 50px;

    @include media ('>=lg') {
        padding-top: 316px;
        padding-bottom: 200px;
    }

    &__text {
        max-width: 657px;
        margin: 0 auto;
        position: relative;

        @include media ('>=sm') {
            &::after {
                position: absolute;
                bottom: -20px;
                right: -125px;
                content: url('../img/bird.png');

            }
        }

        @include media ('>=md') {
            &::after {
                bottom: -50px;
                right: -65px;
            }
        }
        
        @include media ('>=lg') {
            &::after {
                bottom: -100px;
                right: -150px;
              }
            }
            @include media ('>=xl') {
                &::after {
                    bottom: -160px;
                    right: 286px;
                  }
                }
        }

    }


.brown {
    font-family: $font-school;
    font-weight: 700;
    color: #59443F;
    display: block;
    font-size: 40px;
    line-height: 40px;
    text-align: center;
    margin-bottom: 20px;
    z-index: 1000;

    @include media ('>=lg') {
        font-size: 80px;
        line-height: 90px;
    }

    @include media ('>=xl') {
        font-size: 144px;
        line-height: 110px;
    }
}

.black {
    font-family: $font-futura;
    font-size: 30px;
    font-weight: 400;
    line-height: 30px;
    color: #080606;
    text-align: center;
    z-index: 1000;

    @include media ('>=lg') {
        font-size: 65px;
        line-height: 70px;
    }

    @include media ('>=xl') {
        font-size: 80px;
        line-height: 110px;
    }
}