.app {
    background: #FFC59E;
    padding: 50px 0;

    @include media ('>=lg') {
        position: relative;
        padding: 251px 0 345px 0;
    }

    &__suptitle {
        font-family: $font-futura;
        font-size: 30px;
        line-height: 20px;
        color: #fff;

        @include media ('>=lg') {
            padding-left: 230px;
        }
    }

    &__title {
        font-family: $font-school;
        color: #fff;
        font-size: 40px;
        line-height: 60px;
        font-weight: 400;

        @include media ('>=lg') {
            font-size: 80px;
            line-height: 110px;
            width: 826px;
            padding-left: 230px;
        }
    }

    &__buttons {
        display: flex;
        flex-wrap: wrap;

        @include media ('>=lg') {
            padding-left: 230px;
        }
    }

    &__link:nth-child(1) {
        margin-left: 10px;
        align-self: center;
    }
    &__link{
        transition: 0.5s;
    }
&__link:hover{
transform: scale(1.255);
    transition: 0.5s;
}
    &__images {
        display: none;

        @include media ('>=lg') {
            display: block;
            position: absolute;
            right: -90px;
            bottom: 0;

        }

        @include media ('>=xl') {
            right: 20%;
            bottom: 10%;
        }
    }

    &__images img:nth-child(2) {
        display: none;

        @include media ('>=xl') {
            display: block;
            position: absolute;
            top: -90px;
            right: -300px;
        }
    }
}