.pop-up-profile {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(8, 6, 6, 0.5);
    opacity: 0;
    z-index: -1;
    transform: 1s;
}

.pop-up--active {
    opacity: 1;
    z-index: 3000;
    display: flex;
    transition: 1s;

}

.pop-up__top {
    display: flex;
    justify-content: center;
}

.close {
    width: 20px;
    height: 20px;
    margin-top: 20px;
    margin-left: 150px;
    cursor: pointer;
}

.registr {
    margin: 0 auto;
    display: block;
    outline: none;
    border: 1px solid #EDE9E8;
    border-radius: 20px;
    background: #fff;
    margin-bottom: 32px;
    padding: 10px;
}

.more {
    display: block;
    margin: 0 auto;
    margin-bottom: 32px;
    border: 1px solid transparent;
    background: #EDE9E8;
    border-radius: 33.5px;
    padding: 23px 50px;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    color: #fff;
    text-transform: uppercase;
    font-family: $font-futura;
}

.code {
    width: 50px;
    height: 57px;
    background: #FFFFFF;

    /* brown/bg */
    border: 1px solid #EDE9E8;
    border-radius: 20px;
}

.pop-up__middle {
    margin: 0 auto;
    margin-bottom: 32px;
    width: 250px;
}
.pop-up__inner{
    display: flex;
    
}
.pop-up__inner {
    align-self: center;
    width: 320px;
    margin: 0 auto;
    
overflow-x: hidden;
    @include media ('>=lg') {
        width: 400px;
        margin: 0 auto;
    }
}
#card__one{
    transform: translateX(0);
}
#card__two{
    transform: translateX(100%);
}
