.right__card {
    display: flex;
    flex-wrap: wrap;
    background: #FFFFFF;
    border: 1px solid #EDE9E8;
    border-radius: 20px;
    max-width: 909px;
}

.right__card-inner {
    margin-left: 30px;
    padding: 30px 0;
    margin-bottom: 30px;
}


.item span {
    display: block;
    font-family: $font-futura;
    font-size: 15px;
    line-height: 15px;
    font-weight: 400;
    margin-bottom: 10px;
}

.item div {
    font-size: 20px;
    line-height: 25px;
    font-family: $font-futura;
    color: #080606;
    ;
    font-weight: 400;
    margin-bottom: 20px;
    max-width: 404px;
}

.new-pets {
    min-width: 100px;
    width: 100%;
    display: block;
    background: #FFC59E;
    box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    outline: none;
    border: none;
    color: #FFF;
    text-transform: uppercase;

    font-family: $font-futura;
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
    padding-top: 60px;
    padding-bottom: 60px;
    position: relative;
    margin-bottom: 30px;

    @include media ('>=lg') {
        &::after {
            position: absolute;
            top: 0;
            left: 0;
            content: url('../img/newpets.svg');
        }
    }
}

.right-slider {
    max-width: 400px;
    margin-left: 30px;
}

.slide-card {
    margin: 0 auto;
    background: #FFFFFF;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    position: relative;
    margin-bottom: 10px;
    padding-top: 20px;
    width: 350px;

    &::after {
        position: absolute;
        top: 0;
        left: 0;
        content: url('../img/document.svg');
    }
}

.right-next {
    position: absolute;
    top: 95%;
    right: 44%;
    width: 50px;
    height: 50px;
    border: 1px solid transparent;
    border-radius: 50%;
    background: #FFC59E;
    box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.2);

}

.icon-next {
    position: relative;
    display: flex;
    justify-content: center;


    &::after {
        position: absolute;
        transform: rotate(90deg);
        content: url('../img/right.svg');
        align-self: center;
        transition: 0.3s;
    }
}

.right-prev {
    display: none !important;
}