.my-dots {
    list-style-type: none;
    position: absolute;
    top: 150px;
    left: 100px;
}

.my-dots button {
    background: transparent;
    outline: none;
    border: none;
    font-family: $font-futura;
    color: #C3B9B5;
    font-size: 20px;
    line-height: 20px;
    font-weight: 700;
    margin-bottom: 70px;
    position: relative;
}

.my-dots button:hover {
    color: #080606;
    transition: .3s;
}

.my-dots button:hover::after {
    position: absolute;
    top: -1%;
    left: -45%;
    content: '\e807';
    font-size: 45px;
    font-weight: 400;
    font-family: 'my__icon';
    color: #080606;
    animation: transformRotate 1s;
}

@keyframes transformRotate {
    to {
        transform: rotate(360deg);
    }

    from {
        transform: rotate(0deg);

    }
}

.slider__btn {
    border: none;
    outline: none;
    background: #FFC59E;
    color: #FFFFFF;
    margin: 0 auto;
    display: block;
    margin-top: 50px;
    padding: 24px 75px;
    font-family: $font-futura;
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    box-shadow: 0px 4px 20px #FFC59E;
    border-radius: 33.5px;
    text-transform: uppercase;
    transition: 1s;

    @include media ('>=md') {
        margin-left: 100px;
        margin-top: 100px;
    }

    @include media ('>=lg') {
        margin-left: 200px;
        margin-top: 200px;
    }

    @include media ('>=xl') {
        margin-top: 50px;
        margin-left: 479px;
    }
}

.slider__btn:hover {
    background: #59443F;
    border-radius: 33.5px;
    box-shadow: none;
    transition: 1s;
}