.site-footer {
    color: #fff;
    background-color: #59443F;
}


.footer__top {
    padding: 60px 0 0;
    text-align: center;
    zoom: 1;
}

.top__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.footer__logo {
    position: relative;
    padding: 0;
    color: #fff;
    float: left;
    text-align: left;
    font-weight: 700;
    font-size: 30px;
    font-family: $font-school;
    line-height: 45px;
    margin-left: 60px;
    margin-bottom: 39px;
    display: block;
width: 212px;
    position: relative;

    @include media ('>=lg') {
        margin-left: 200px;
    }

    &::after {
        position: absolute;
        top: -10px;
        left: -100px;
        content: url('../img/footer-logo.svg');
        width: 100%;
        height: 100%;
    }
}
.politics{
    width: 192px;
    margin-top: 39px;
}
.politics__link {
    
    font-family: $font-futura;
    line-height: 30px;
    font-size: 20px;
    color: #fff;
    
}
.footer__lists-wrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.list{
    list-style-type: none;
    margin-top: 39px;
    &__link{
        font-family: $font-futura;
    line-height: 30px;
    font-size: 20px;
    color: #fff;
    }
}
.list-icons{
    list-style-type: none;
    display: flex;
}
.emails__link{
    display: block;
    font-family: $font-futura;
    line-height: 30px;
    font-size: 20px;
    color: #fff;
    margin: 0 auto;
}