.pop-up {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(8, 6, 6, 0.5);
    opacity: 0;
    z-index: -1;
    transform: 1s;

}

.pop-up--active {
    opacity: 1;
    z-index: 3000;
    display: flex;
    transition: 1s;

}

.pop-up__top {
    display: flex;
    justify-content: center;
}

.close {
    width: 20px;
    height: 20px;
    margin-top: 20px;
    margin-left: 150px;
    cursor: pointer;
}

.scroll {
    height: 100px;
    overflow-y: scroll;
}

.pop-up__title {
    padding-top: 50px;
    text-align: center;
    margin-bottom: 40px;
    color: #080606;
    font-size: 30px;
    line-height: 40px;
    font-family: $font-school;
    position: relative;

}

.pop-up__card {
    align-self: center;
    width: 320px;
    margin: 0 auto;
    background: #FFFFFF;
    box-shadow: 2px 2px 20px rgba(155, 82, 50, 0.1);
    border-radius: 20px;

    @include media ('>=lg') {
        width: 400px;
        margin: 0 auto;
    }
}

.pop-up__list {
    list-style-type: none;
}

.pop-up__list li {
    cursor: pointer;
    color: #080606;
    font-family: $font-futura;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 30px;
}

.pop-up__list li:hover {
    color: #FFC59E;
    transition: 0.5s;
}