.img {
    width: auto;
}

.right__img {
    height: auto;
    width: 100%;
    object-fit: cover;
}

.special {
    &__inner {
        display: flex;
        flex-wrap: wrap;
        max-width: 909px;
    }

    &__item {
        display: flex;
        flex-wrap: wrap;
    }

    &__card {
        display: flex;
        max-width: 442px;
        background: #FFFFFF;
        box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.1);
        border-radius: 20px;
        margin-bottom: 25px;
        @include media ('>=lg'){
            margin-left: 25px;
            padding: 30px;
        }
    }
}
.card-left{
    width: auto;
}
.card-left img{
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 50%;
    padding: 20px;
}
.name-doctor{
    font-family: $font-futura;
    color: #080606;
    font-size: 15px;
    font-weight: 400;
    margin-bottom: 20px;
    @include media ('>=lg'){
        font-size: 20px;
        line-height: 20px;
    }
    
}
.doctor{
    font-family: $font-futura;
    color: #080606;
    font-size: 15px;
    font-weight: 400;
    margin-bottom: 30px;
    @include media ('>=lg'){
        font-size: 20px;
        line-height: 20px;
    }
}