.special__wrap {
    max-width: 1265px;
    margin: 0 auto;
    margin-top: 70px;
    @include media ('>=lg'){
        margin-top: 100px;
    }
}

.special-slider {
    position: relative;
}

.slide-special {
    margin: 0 auto;
    @include media ('>=sm'){
        margin: 0 auto;
        margin-left: -30px;
        margin-right: -65px;
    }

}

.slide-special img {
    border: 1px solid transparent;
    border-radius: 50%;
}

.special-prev {
    position: absolute;
    top: 30%;
    left: -5%;
    width: 40px;
    height: 40px;
    border: 1px solid transparent;
    border-radius: 50%;
    background: #FFC59E;
    box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.2);


}

.icon-left {
    position: relative;

    display: flex;
    justify-content: center;

    &::after {
        position: absolute;
        content: url('../img/left.svg');
        align-self: center;
        transition: 0.3s;
    }

}

.special-prev:hover .icon-left::after {
    transform: scale(1.3);
    transition: 0.3s;
}

.special-next {
    position: absolute;
    top: 30%;
    right: -5%;
    width: 40px;
    height: 40px;
    border: 1px solid transparent;
    border-radius: 50%;
    background: #FFC59E;
    box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.2);
}

.icon-right {
    position: relative;
    display: flex;
    justify-content: center;

    &::after {
        position: absolute;

        content: url('../img/right.svg');
        align-self: center;
        transition: 0.3s;
    }
}

.special-next:hover .icon-right::after {
    transform: scale(1.3);
    transition: 0.3s;
}