.services{
    @include media ('>=xl'){
        padding-top: 150px;
    }
    background-color: #FFFEFE;
    &__inner{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    &__title{
        font-family: $font-school;
        color: #59443F;
        font-size: 20px;
        font-weight: 400;
        line-height: 40px;
        margin: 0 0 0 24px;
    }
    &__card{
        width: 220px;
        background-color: #fff;
        height: 220px;
        border: 1px solid transparent;
        border-radius: 20px;
        box-shadow: 0 0 5px rgba($color: #000000, $alpha: 0.2);
        margin-bottom: 24px;
        display: block;
        position: relative;
        transition: .3s;
        @media (min-width: 546px) and (max-width: 576px){
            margin-left: 24px;
        }
        @include media ('>=sm'){
            margin-left: 24px;
        }
        @include media ('>=md'){
            width: 320px;
        }
    }
    &__card span p{
        position: absolute;
        bottom: 0;
        left: 24px;
        
    }
    &__card:hover{
       border-color: #59443F;
        
        transition: 1s;
    }
    &__card:hover .hirurgicon-
    {
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .nevrolicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .dantisticon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .labicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .ekzoticicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .onkoicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .terapicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .dermoicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .vizualicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .ophtolicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .cardioicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .endocricon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .aptekaicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .ortopedicon-{
        color: #59443F;
        transition: 1s;
    }
    &__card:hover .groomingicon-{
        color: #59443F;
        transition: 1s;
    }

}

