.contacts {
    background: #FFFEFE;
    padding-top: 50px;
    padding-bottom: 70px;

    @include media ('>=lg') {
        padding: 209px 0 250px 0;
    }

    &__title {
        font-size: 40px;
        line-height: 40px;
        font-weight: 400;
        font-family: $font-school;
        color: #080606;
    }
    &__inner{
        @include media ('>=lg'){
            display: flex;
        }
    }
}

.left__title {
    font-family: $font-school;
    font-size: 30px;
    line-height: 40px;
    color: #080606;
    font-weight: 400;
}

.left__item span {
    display: block;
}

.adress {
    font-size: 20px;
    line-height: 30px;
    font-family: $font-futura;
    color: #59443F;
    font-weight: 400;
    margin-bottom: 23px;
}

.tel {
    font-size: 20px;
    line-height: 30px;
    font-family: $font-futura;
    color: #59443F;
    font-weight: 400;
}

.clock {
    font-size: 20px;
    line-height: 30px;
    font-family: $font-futura;
    color: #59443F;
    font-weight: 400;
    margin-top: 23px;
}
.right{
    margin-left: 30px;
    margin-top: 30px;
}