@charset "UTF-8";

@font-face {
  font-family: 'Futura New';
  src: url("FuturaNewBold-Reg.eot");
  src: local("Futura New Bold Reg"), local("FuturaNewBold-Reg"), url("../fonts/FuturaNewBold-Reg.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Futura New';
  src: url("FuturaNewBook-Reg.eot");
  src: local("Futura New Book Reg"), local("FuturaNewBook-Reg"), url("../fonts/FuturaNewBook-Reg.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'SchoolBook';
  src: url("../fonts/SchoolBook Regular") format("ttf");
  font-weight: normal;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
}

body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

.container {
  max-width: calc(100% - 80px);
  margin: 0 auto;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

::-moz-selection {
  background: #d37f02;
  text-shadow: none;
}

::selection {
  background: #d37f02;
  text-shadow: none;
}

a:not([class]) {
  color: #07bfbc;
  text-decoration: underline;
  transition: all 0.3s;
}

a:not([class]):hover {
  color: #019ca4;
  text-decoration: none;
}

.list-unstyled {
  padding-left: 0;
  list-style: none;
}

img {
  vertical-align: middle;
  line-height: 1;
}

.image-cover img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
}

.hidden {
  visibility: hidden;
  height: 0;
}

html {
  font-size: 100%;
}

body {
  color: #C3B9B5;
  font-family: "Futura New", sans-serif;
  font-weight: 400;
  font-size: 1rem;
}

.alignleft {
  float: left;
  margin-right: 24px;
  margin-bottom: 24px;
}

.alignright {
  float: right;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  margin-top: 2.5rem;
  margin-bottom: 2rem;
  line-height: 1.3;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child {
  margin-top: 0;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.75rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 1.125rem;
}

h6 {
  font-size: 1rem;
}

p {
  margin-bottom: 2rem;
}

ul:not([class]) {
  list-style: none;
  padding-left: 0;
  margin-bottom: 2rem;
}

ul:not([class]) > li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 1rem;
}

ul:not([class]) > li:first-child {
  margin-top: 0;
}

ul:not([class]) > li:last-child {
  margin-bottom: 0;
}

ul:not([class]) > li::before {
  content: "";
  position: absolute;
  background-color: #07bfbc;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  top: 9px;
  left: 0;
}

ul:not([class]) > li > ul {
  margin-top: 1rem;
  margin-bottom: 0;
}

ul:not([class]) > li > ul > li {
  margin-bottom: 0.5rem;
}

ul:not([class]) > li > ul > li::before {
  content: "";
  background-color: #07bfbc;
  border-radius: 50%;
  width: 5px;
  height: 5px;
  top: 9px;
}

ul:not([class]) > li > ul > li > ul > li::before {
  background-color: #fff;
  border: 2px solid #07bfbc;
}

ol:not([class]) {
  counter-reset: li;
  list-style: none;
  padding-left: 0;
  margin-bottom: 2rem;
}

ol:not([class]) > li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 1rem;
}

ol:not([class]) > li:first-child {
  margin-top: 0;
}

ol:not([class]) > li:last-child {
  margin-bottom: 0;
}

ol:not([class]) > li::before {
  content: counters(li, ".") ". ";
  counter-increment: li;
  position: absolute;
  left: 0;
}

ol:not([class]) > li > ol {
  margin-top: 1rem;
  margin-bottom: 0;
}

ol:not([class]) > li > ol > li {
  padding-left: 2rem;
}

ol:not([class]) > li > ul {
  margin-top: 1rem;
}

ol:not([class]) > li > ul > li::before {
  content: "";
  background-color: #07bfbc;
  border-radius: 50%;
  width: 5px;
  height: 5px;
  top: 9px;
}

.content {
  line-height: 1.6;
}

.content img {
  max-width: 100%;
  height: auto;
}

.content iframe,
.content embed {
  width: 100%;
  border: none !important;
}

.content figure {
  float: none;
}

.content figcaption {
  font-weight: 600;
  font-style: italic;
  margin-top: 12px;
  font-weight: 0.9375rem;
}

table:not([class]) {
  background-color: transparent;
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
  margin-bottom: 2rem;
}

table:not([class]) th,
table:not([class]) td {
  background-clip: padding-box;
  border: 1px solid transparent;
  vertical-align: top;
  padding: 1rem;
}

table:not([class]) th:first-child,
table:not([class]) td:first-child {
  border-left: 0;
}

table:not([class]) th:last-child,
table:not([class]) td:last-child {
  border-right: 0;
}

table:not([class]) thead th,
table:not([class]) thead td {
  font-weight: 700;
  background-color: #e9e9e9;
  color: #C3B9B5;
}

table:not([class]) tbody tr:nth-child(odd) th,
table:not([class]) tbody tr:nth-child(odd) td {
  background-color: #fff;
}

table:not([class]) tbody tr:nth-child(even) th,
table:not([class]) tbody tr:nth-child(even) td {
  background-color: #f5f5f5;
}

table:not([class]) tbody tr:last-child th,
table:not([class]) tbody tr:last-child td {
  border-bottom: 1px solid #f1f1f1;
}

.table-responsive {
  display: block;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  margin-bottom: 2rem;
}

.table-responsive > table {
  margin-bottom: 0;
}

@keyframes onAutoFillStart {
  from {

  }

  to {

  }
}

@keyframes onAutoFillCancel {
  from {

  }

  to {

  }
}

@keyframes shake {
  from, to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  50% {
    -webkit-transform: translate3d(-2px, 0, 0);
    transform: translate3d(-2px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
    transform: translate3d(2px, 0, 0);
  }
}

@keyframes arrow-in {
  0% {
    opacity: 1;
    transform: translate(0, 0);
  }

  90% {
    opacity: 1;
    transform: translate(0, 0);
  }

  100% {
    opacity: 0;
    transform: translate(0, 100%);
  }
}

@keyframes arrow-out {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }

  90% {
    opacity: 0;
    transform: translate(0, 0);
  }

  100% {
    opacity: 1;
    transform: translate(0, 100%);
  }
}

@keyframes slide-in-down {
  from {
    top: -100%;
  }

  to {
    top: 0;
  }
}

@keyframes slide-in-up {
  from {
    top: 100%;
  }

  to {
    top: 0;
  }
}

@keyframes slide-out-down {
  from {
    top: 0;
  }

  to {
    top: 100%;
  }
}

@keyframes slide-out-up {
  from {
    top: 0;
  }

  to {
    top: -100%;
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* Анимация - эффект бесконечного вращения */

.spin {
  animation-name: spin;
  animation-duration: 600ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  z-index: 100;
}

video {
  width: 100%;
  height: auto;
  max-height: 100%;
}

.video {
  position: relative;
  padding-bottom: 56.25%;
  margin-bottom: 2rem;
  max-width: 100%;
  width: 100%;
}

.video:last-child {
  margin-bottom: 0;
}

.video iframe {
  border: 0;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}

html,
body {
  height: 100%;
}

body {
  background: #fff;
  width: 100%;
}

.root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  overflow: hidden;
}

.site-main {
  flex: 1 0 auto;
  padding: 0;
  background-color: #fff;
  position: relative;
}

.site-main.home {
  padding: 0;
}

.page-content-wrap--800 {
  max-width: 800px;
}

.page-content-wrap--800-center {
  max-width: 800px;
  margin: 0 auto;
}

.layout-left-sidebar .page-content-wrap {
  margin: 0;
}

.layout-right-sidebar .page-content-wrap {
  margin: 0;
}

.header-top {
  display: none;
}

.header-top-inner {
  display: flex;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  position: relative;
  margin-top: 80px;
  margin-left: 50px;
}

.logo__img {
  display: none;
}

.logo__text {
  display: none;
  font-size: 18px;
  color: #000;
  text-decoration: none;
  margin-left: 16px;
  line-height: 1;
}

a.h-phone {
  color: #000;
  text-decoration: none;
}

.h-phone {
  display: flex;
  align-items: center;
  margin-left: 64px;
  font-weight: 600;
  transition: all 0.3s;
}

.h-phone i.icon-phone {
  margin-right: 16px;
  font-size: 24px;
  color: #7D7D7D;
  transition: all 0.3s;
}

.h-phone__info span {
  display: block;
  line-height: 1;
  color: #7D7D7D;
  font-size: 12px;
  text-align: right;
}

.h-phone:hover {
  color: #07bfbc;
}

.h-phone:hover i.icon-phone {
