}

.drawer-scrim {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  transition-property: opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  background-color: #000;
  opacity: 0.5;
}

.drawer {
  position: fixed;
  top: 0;
  width: 320px;
  right: -320px;
  height: 100vh;
  background-color: #fff;
  z-index: 2000;
  transition: transform 0.3s;
  overflow: hidden;
}

.drawer.opened {
  box-shadow: 0px 8px 10px -5px rgba(0, 0, 0, 0.2), 0px 16px 24px 2px rgba(0, 0, 0, 0.14), 0px 6px 30px 5px rgba(0, 0, 0, 0.12);
  transform: translateX(-320px);
}

.drawer-inner {
  padding: 88px 24px 32px 24px;
  position: relative;
  overflow-y: auto;
  height: 100%;
}

.drawer-logo {
  position: absolute;
  top: 16px;
  left: 24px;
  width: 40px;
  height: auto;
}

.drawer-login-btn {
  position: absolute;
  top: 24px;
  left: 24px;
  text-decoration: none;
  color: #C3B9B5;
}

.drawer-login-btn i {
  font-size: 32px;
}

.drawer-menu {
  list-style: none;
