@font-face {
    font-family: 'Futura New';
    src: url('FuturaNewBold-Reg.eot');
    src: local('Futura New Bold Reg'), local('FuturaNewBold-Reg'),
        
        url('../fonts/FuturaNewBold-Reg.woff') format('woff');
        
    font-weight: bold;
    font-style: normal;
}
@font-face {
    font-family: 'Futura New';
    src: url('FuturaNewBook-Reg.eot');
    src: local('Futura New Book Reg'), local('FuturaNewBook-Reg'),
        
        url('../fonts/FuturaNewBook-Reg.woff') format('woff');
        
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'SchoolBook';
    src: url('../fonts/SchoolBook Regular') format('ttf');
    font-weight: normal;
    font-style: normal;
}

