.wrapper {
    &__inner {
        display: flex;
        margin-top: 50px;
        flex-wrap: wrap;

        @include media ('>=lg') {
            flex-wrap: nowrap;
        }
    }
}

.left {
    &__title {
        font-size: 20px;
        line-height: 30px;
        font-family: $font-school;
        color: #080606;
        font-weight: 400;

        @include media ('>=md') {
            font-size: 40px;
            line-height: 50px;
        }

        @include media ('>=lg') {
            font-size: 80px;
            line-height: 110px;
        }
    }

    &__link {
        font-size: 10px;
        line-height: 20px;
        font-family: $font-futura;
        color: #C3B9B5;
        ;
        font-weight: 700;
        text-transform: uppercase;
        display: block;
        margin-bottom: 30px;

        @include media ('>=md') {
            font-size: 20px;
        }

        @include media ('>=lg') {
            font-size: 20px;
            line-height: 30px;
        }
    }
}

.right {
    width: 100%;

    @include media ('>=md') {
        margin-left: 70px;
    }

    @include media ('>=lg') {
        margin-left: 200px;
    }

    &__title {
        font-size: 15px;
        line-height: 30px;
        font-family: $font-school;
        color: #080606;
        font-weight: 400;

        @include media ('>=md') {
            font-size: 20px;
            line-height: 30px;
        }

        @include media ('>=lg') {
            font-size: 30px;
            line-height: 40px;
        }
    }

}

.name {
    display: block;
    font-size: 20px;
    line-height: 20px;
    color: #080606;
    font-family: $font-futura;
    position: relative;
    width: 219px;
    margin-bottom: 10px;

    &::after {
        position: absolute;
        top: 0;
        left: -60px;
        content: url('/img/avatar-slider.png');
        border-radius: 50%;
    }
}

.date {
    display: block;
}

.slider-terap {
    position: relative;

}

.terap-prev {
    display: none !important;
}

.slide-terap {
    max-width: 320px;
    padding: 70px 0;
    padding-right: 20px;


}

.terap-next {
    position: absolute !important;
    top: 43% !important;
    left: 62.6%;
    width: 40px;
    height: 40px;
    border: 1px solid transparent;
    border-radius: 50%;
    background: #FFC59E;
    box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.2);

}

.terap-prev {
    display: none;
}

.terap-card {
    padding: 20px 51px 20px 86px;
    height: 150px;
    background: #FFFFFF;
    box-shadow: 2px 2px 20px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
}

.terap-info {
    // max-width: 700px;
    // @include media ('>=xl') {
    //     max-width: 909px;
    // }

    &__title {
        font-family: $font-school;
        font-size: 15px;
        line-height: 20px;
        color: #080606;
        font-weight: 400;

        @include media ('>=lg') {
            font-size: 30px;
            line-height: 40px;
        }
    }

    &__text {
        font-size: 20px;
        line-height: 30px;
        color: #C3B9B5;
        font-weight: 400;
        font-family: $font-futura;
    }
}

.terap-info__title {
    font-size: 20px;
    line-height: 30px;
    color: #080606;
    font-family: $font-school;
    font-weight: 400;
}

.info__img {
    max-width: 100%;
    margin: 0 auto;
    margin-bottom: 50px;
}
.service-wrap{
    background: #EDE9E8;
}
.service-slider {
    
    position: relative;
    padding-left: 100px;
    padding-top: 70px;
}

.all__services {
    font-family: $font-school;
    font-weight: 400;
    color: #080606;
    font-size: 30px;
    line-height: 40px;
    margin-left: 80px;
    padding-top: 40px;
}

.service-next {
    position: absolute;
    top: 50%;
    left: 88.5%;
    width: 40px;
    height: 40px;
    border: 1px solid transparent;
    border-radius: 50%;
    background: #FFC59E;
    box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.2);
}

.service-prev {
    display: none !important;
}

.service-slide+.slick-slide {
    display: block;
    color: #fff;
    border-radius: 10px;

    transform: scale(.9);
    opacity: 0.7;

    transition: .5s ease;
}

.service-slide+.slick-active {
    transform: scale(1);
    opacity: 1;
}